import { defineStore } from 'pinia'


export const useMainStore = defineStore('main', {
  state: () => ({ 
    count: 10,
    drawer: false,
    openFilter: false,
    menuSelectedItem: 0,
    menuItems: [],
    menuVisible: true

  }),
  getters: {
    double: state => state.count * 2,
  },
  actions: {
    increment() {
      this.count++
    },
    clearItem()
    {
      this.menuItems = [];
      this.menuVisible = false;
    },
    addItem(item) {
      this.menuItems = [];
      item.map((i)=>{
        this.menuItems.push(i); 
      });
      this.menuSelectedItem = 0;
      this.menuVisible = true;
    }
  },
})
