<template>
  <v-app>
    <a hidden ref="outLink"  v-bind:href="exitlink"></a>
    <v-dialog v-model="mainObj.openAlert" persistent>
      <v-card>
        <v-card-text>{{mainObj.alertTitle}}</v-card-text>
        <v-card-title>{{mainObj.alertText}}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="handleClose()">ОК</v-btn>
          <v-btn
            v-if="mainObj.alertConfirm"
            color="green darken-1"
            text
            @click="mainObj.openAlert = false"
          >Отмена</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="openAlert" persistent>
      <v-card style="padding: 20px;">
        <v-card-title class="body-2">Сообщить о проблеме</v-card-title>
        <v-textarea></v-textarea>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn rounded color="green" @click="openAlert = false">Отправить</v-btn>
          <v-btn rounded color="green" @click="openAlert = false">Отмена</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-app-bar
      app
      dense
      dark
      color="primary"
    >
    
    <v-app-bar-nav-icon  @click="store.drawer = !store.drawer" ></v-app-bar-nav-icon>
    <v-toolbar-title class="font-weight-thin overline">UTG GROUP</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn rounded x-small @click="navi('/account')" 
    color="success"
    dark
    >личный кабинет</v-btn>
    
    <v-btn rounded x-small @click="navi('/dct')"  style="margin-left:10px">Документы для персонала</v-btn> 
    <!--<router-link to="/dct">Документы для персонала</router-link>-->
    
    <v-btn rounded x-small @click="navi('/managment')"  style="margin-left:10px">Модуль СМК</v-btn> 
    
    <v-btn rounded x-small color="warning" @click="openAlert = true" style="margin-left:10px">Обратная связь</v-btn> 
    <v-spacer></v-spacer>
    <v-badge
        content="9+"
        color="red"
        overlap
        style="margin: 20px"
    >

    <v-icon @click="navi('/account/journal')">mdi-bell-ring</v-icon>
    </v-badge>
    
    <v-list-item-avatar>
              <v-img :src="avatar"></v-img>
    </v-list-item-avatar>
    <v-menu left >
          <template v-slot:activator="{ on, attrs }">
            <v-btn icon v-bind="attrs" v-on="on">
              <v-icon>mdi-chevron-down</v-icon>
            </v-btn>
          </template>
          <v-list v-if ="store.menuVisible" >
      <v-list-item-group
        v-model="store.menuSelectedItem"
      >
        <v-list-item
          v-for="(item, i) in store.menuItems"
          :key="i"
          @click="menuclick(item)"
        >
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.text"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>

           <v-list-item key="10000" @click="exit()">      
           <v-list-item-icon>
             <v-icon>mdi-exit-to-app</v-icon>
           </v-list-item-icon>
           <v-list-item-content>
               <v-list-item-title>Выход</v-list-item-title>
           </v-list-item-content>        
           </v-list-item>  
        
        
      </v-list-item-group>
    </v-list>              
    </v-menu>
    </v-app-bar>  
    <v-main>
      <router-view/>
    </v-main>  
    </v-app>
</template>

<script>
import { useMainStore } from './store/index'
import { mainObj, baseUrl, prodaction } from "./main"

export default {
  name: 'AppPortal',
  
  setup() {
      const store = useMainStore()
      return { store }

    },

  data: () => ({
    openAlert:false,
    mainObj: mainObj,
    ad_name: "XXX"
  }),
  computed:
  {
    avatar: function()
    {
      return baseUrl + "Docfiles/getphoto?audtuser=" + this.ad_name;
    },
    exitlink: function()
    {
      return baseUrl + "Home/logout";
    }
  },

  methods: {
    exit: function()
    {
      this.$refs.outLink.click();
    },
    navi: function(lnk) {
      //alert(lnk);
      this.$router.push(lnk);
    },
    menuclick: function(item)
    {
      //alert(item.text);
      if (item.action)
         item.action();
    },
    handleClose: function() {
      mainObj.openAlert = false;
      if (mainObj.alertConfirm) mainObj.confirmAction();
    }
  },
  mounted: async function() 
    {
      
      const url = baseUrl + "React/FinderStart";
      let bd = new FormData();
      bd.append("id", 1467);
      bd.append("mode", "data");
      const response = await fetch(url, {
          method: "POST",
          mode: prodaction ? "no-cors" : "cors",
          cache: "no-cache",
          credentials: prodaction ? "include" : "omit",
          body: bd
      });
      
      let tableData = await response.json();
      this.ad_name = tableData.Account;
    }
};
</script>
